<template>
  <div>
    
  </div>
</template>
<script lang="ts">
import { Component, Vue } from 'vue-property-decorator';
import { 
    Scene, 
    PerspectiveCamera, 
    WebGLRenderer, 
    AxesHelper, 
    BoxGeometry, 
    MeshBasicMaterial, 
    AmbientLight, 
    Mesh, 
    TextureLoader, 
    GridHelper,
    PlaneGeometry,
    MeshPhysicalMaterial,
    DoubleSide,
    SpotLight,
    SpotLightHelper,
    CylinderGeometry,
    Vector2,
    Raycaster,
} from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import GUI from 'lil-gui'
const TWEEN = require('@tweenjs/tween.js')
@Component({
  components: {
  },
})
export default class CarDemo extends Vue {
  private let: any;
  private scene: any; 
  private camera: any; 
  private renderer: any; 
  private circle: any;
  private axesHelper: any;
  private orbitControls: any;
  private tween: any;
  private mesh: any;
  private carOpenStatus: any = 'close'
  private carCoverOpenStatus: any = 'close'
  private carBackCoverOpenStatus: any = 'close'
  private doorsField: any = []
  private carFrontCover: any = []
  private carbackCover: any = []
  private spotLight: any;
  private footMaterial: any = new MeshPhysicalMaterial({
    color: 0x000000,
    metalness: 1,
    roughness: 0.5,
    clearcoat: 1.0,
    clearcoatRoughness: 0.03
  })
  private bodyMaterial: any = new MeshPhysicalMaterial({
    color: 0xaea8ff,
    metalness: 1,
    roughness: 0.5,
    clearcoat: 1.0,
    clearcoatRoughness: 0.03
  })
  private glassMaterial: any = new MeshPhysicalMaterial({
    color: 0x000000,
    transparent: true, // 透明度设置为 true
    opacity: 0.9, // 设置透明度
    roughness: 1,
    metalness: 0,
    envMapIntensity: 1,//需要搭配transparent
    transmission: 0.95, // 折射度，表示光线经过材料时的衰减程度
    clearcoat: 1,
    clearcoatRoughness: 0,
    // refractionRatio: 1.5, // 折射率，控制光的折射程度
  })
  private steeringWheelMaterial: any = new MeshPhysicalMaterial({
    color: 0xff0000,
    metalness: 1,
    roughness: 0.5,
    clearcoat: 1.0,
    clearcoatRoughness: 0.03
  });

  // methods
  
  private init() {
    this.initScene()
    this.initCamera()
    this.initRenderer()
    this.initAxesHelper()
    this.initOrbitControls()
    // initGridHelper()
    this.loadCarModel()
    this.initAmbientLight()
    this.initFloor()
    this.initSpotLight()
    this.initCylinderGeometry()
    this.initGui()
  }
  private renderI(time?: any) {
    this.renderer.render(this.scene, this.camera)
    requestAnimationFrame(this.renderI)
    this.orbitControls.update()
    this.tween && this.tween.update(time)
  }
  
  // 初始化场景
  private initScene() {
    this.scene = new Scene()
  }

  // 初始化相机（透视相机）
  private initCamera() {
    this.camera = new PerspectiveCamera(10, window.innerWidth / window.innerHeight, 1.8, 1000);
    // camera.position.set(23, 32, -15)
    this.camera.position.set(25, 3.5, -26)
  }

  // 初始化渲染器
  private initRenderer() {
    this.renderer = new WebGLRenderer({
        antialias: true
    })
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    // 支持阴影
    this.renderer.shadowMap.enabled = true
    document.body.appendChild(this.renderer.domElement)
  }

  // 初始化坐标轴
  private initAxesHelper() {
    this.axesHelper = new AxesHelper();
    this.scene.add(this.axesHelper);
  }

  // 初始化轨道控制器：提供360deg的查看
  private initOrbitControls() {
    this.orbitControls = new OrbitControls(this.camera, this.renderer.domElement)
    
    // 惯性
    this.orbitControls.enableDamping = true

    this.orbitControls.autoRotate = true
    
    // // 缩放比例控制
    this.orbitControls.maxDistance = 50
    this.orbitControls.minDistance = 1

    // 角度
    this.orbitControls.minPolarAngle = 0
    this.orbitControls.maxPolarAngle = 1.5
  }

  // 绘制地面网格
  private initGridHelper() {
    let grid = new GridHelper(120, 180, 'red', 0xffffff)
    grid.material.opacity = 0.5
    grid.material.transparent = true
    this.scene.add(grid)
  }

  // 加载模型
  private loadCarModel() {
    let that: any = this;
    that.$Bus.$emit('visibleMask', true)
    new GLTFLoader().load('/threejsCar/scene.glb', (gltf) => {
      const model = gltf.scene
      model.scale.set(0.01, 0.01, 0.01)
      model.position.y = 1.24
      that.$Bus.$emit('visibleMask', false)
      // 设置车身颜色
      model.traverse((obj: any) => {
          if(obj.name.indexOf('wheels') != -1) {
              obj.material = this.footMaterial
          } else if(obj.name.indexOf('glass') != -1) {
              obj.material = this.glassMaterial
          } else if(obj.name === 'door_rf') {
            this.doorsField.push(obj)
          } else if(obj.name === 'bonnet_ok') {
            this.carFrontCover.push(obj)
          } else if(obj.name === 'boot_dummy' || obj.name === 'JUST_BLACK001') {
            this.carbackCover.push(obj)
          } else if(obj.name === 'movsteer_10_movsteer_100_0') {
              obj.material = this.steeringWheelMaterial
          } else {
              obj.material = this.bodyMaterial
          }
          // 模型产生阴影
          obj.castShadow = true
      })
      this.scene.add(model)
    })
    
  }

  // 设置环境光
  private initAmbientLight() {
    let ambientLight = new AmbientLight('#fff', 0.5)
    this.scene.add(ambientLight)
  }

  // 绘制地板
  private initFloor() {
      const floorGeometry = new PlaneGeometry(120, 120)
      let texture = new TextureLoader().load('/threejsCar/Maple.jpg')
      const material = new MeshPhysicalMaterial({
          side: DoubleSide, // 双面绘制
          color: 0x808080,
          metalness: 0, // 非金属度
          roughness: 0.1, // 粗糙程度, 地面反射强烈, 越小越光滑
          map: texture
      })
      const mesh = new Mesh(floorGeometry, material)
      mesh.rotation.x = Math.PI / 2
      mesh.position.y = 0.5
      // 接收模型的阴影
      mesh.receiveShadow = true
      this.scene.add(mesh)
  }

  // 设置聚光灯
  private initSpotLight() {
    this.spotLight = new SpotLight( 0xffffff, 200 );
    this.spotLight.position.set( 2.5, 5, 4.5 );
    this.spotLight.angle = Math.PI / 6;
    this.spotLight.penumbra = 1;
    this.spotLight.decay = 2;
    this.spotLight.distance = 0;
    // this.spotLight.map = textures[ 'disturb.jpg' ];

    this.spotLight.castShadow = true;
    this.spotLight.shadow.mapSize.width = 4096;
    this.spotLight.shadow.mapSize.height = 4096;
    this.spotLight.shadow.camera.near = 1;
    this.spotLight.shadow.camera.far = 10;
    this.spotLight.shadow.focus = 1;
    this.scene.add( this.spotLight );
  }
  
  // 绘制圆柱体 模拟 展厅
  private initCylinderGeometry() {
    // 顶部半径,底部半径,高度,折射段数(由一个三角形组成,数值越大呈现的越圆滑)
    const geometry = new CylinderGeometry(80, 80, 120, 64)
    const material = new MeshPhysicalMaterial({
        color: 0x6c6c6c,
        side: DoubleSide
    })
    const cylinder = new Mesh(geometry, material)
    this.scene.add(cylinder)
  }

  private initGui() {
    let obj = {
        footColor: '#000000',
        bodyColor: '#aea8ff',
        steeringWheelColor: '#ff0000',
        carOpen: this.carOpen,
        carClose: this.carClose,
        carIn: this.carIn,
        carOut: this.carOut,
        carFrontCoverOpen: this.carFrontCoverOpen,
        carFrontCoverClose: this.carFrontCoverClose,
        carBackCoverOpen: this.carBackCoverOpen,
        carBackCoverClose: this.carBackCoverClose,
        test: () => {
            console.log(this.orbitControls.target,this.camera)
        }
    }
    let gui = new GUI()
    gui.addColor(obj, 'footColor').name('车胎颜色').onChange((val: any) => {
      this.footMaterial.color.set(val)
    });
    gui.addColor(obj, 'bodyColor').name('车身颜色').onChange((val: any) => {
      this.bodyMaterial.color.set(val)
    });
    gui.addColor(obj, 'steeringWheelColor').name('方向盘颜色').onChange((val: any) => {
      this.steeringWheelMaterial.color.set(val)
    });
    gui.add(obj, 'carOpen').name('打开车门')
    gui.add(obj, 'carClose').name('关闭车门')
    gui.add(obj, 'carIn').name('车内视角')
    gui.add(obj, 'carOut').name('车外视角')
    gui.add(obj, 'carFrontCoverOpen').name('打开车前盖')
    gui.add(obj, 'carFrontCoverClose').name('关闭车前盖')
    gui.add(obj, 'carBackCoverOpen').name('打开车后盖')
    gui.add(obj, 'carBackCoverClose').name('关闭车后盖')

    gui.add(obj, 'test').name('获取视野视角')

  }

  // 打开车门
  private carOpen() {
    this.carOpenStatus = 'open'
    this.doorsField.forEach((item: any) => {
      this.setAnimationDoor({z: 0}, { z: 0.8 }, item)
    })
  }

  // 关闭车门
  private carClose() {
    this.carOpenStatus = 'close'
    this.doorsField.forEach((item: any) => {
      this.setAnimationDoor({ z: 0.8 }, { z: 0}, item)
    })
  }

  // 车内视角
  private carIn() {
    // {x: 0.14420308091740558, y: 2.424410730488036, z: 1.538314433535965} Vector3 {x: 0, y: 0.5, z: -3}
    this.orbitControls.enableRotate = false
    this.setAnimationCamera({ cx: 22.99, cy: 32, cz: -15, ox: 0, oy: 0, oz: 0 }, { cx: 0.14, cy: 2.4, cz: 1.5, ox: 0, oy: -0.5, oz: -6 });
  }

  // 车外视角
  private carOut() {
    this.orbitControls.enableRotate = true
    this.setAnimationCamera({ cx: 0.14, cy: 2.4, cz: 1.5, ox: 0, oy: -0.5, oz: -6 }, { cx: 22.99, cy: 32, cz: -15, ox: 0, oy: 0, oz: 0 });
  }

  // 打开车前盖
  private carFrontCoverOpen() {
    this.carCoverOpenStatus = 'open'
    this.carFrontCover.forEach((item: any) => {
      this.setAnimationCover( {x: 0}, { x: 0.6 }, item)
    })
  }

  // 关闭车前盖
  private carFrontCoverClose() {
    this.carCoverOpenStatus = 'close'
    this.carFrontCover.forEach((item: any) => {
      this.setAnimationCover({ x: 0.6 }, {x: 0}, item)
    })
  }
  // 打开车后盖
  private carBackCoverOpen() {
    this.carBackCoverOpenStatus = 'open'
    this.carbackCover.forEach((item: any) => {
      this.setAnimationCover( {x: 0}, { x: -0.8 }, item)
    })
  }

  // 关闭车后盖
  private carBackCoverClose() {
    this.carBackCoverOpenStatus = 'close'
    this.carbackCover.forEach((item: any) => {
      this.setAnimationCover({ x: -0.8 }, {x: 0}, item)
    })
  }

  private setAnimationCover(start: any, end: any, mesh: any) {

    this.tween = new TWEEN.Tween(start) // Create a new tween that modifies 'coords'.
      .to(end, 1000) // Move to (300, 200) in 1 second.
      .easing(TWEEN.Easing.Quadratic.InOut) // Use an easing private to make the animation smooth.
      .onUpdate((that: any) => {
          mesh.rotation.x = that.x
      })
      .start()
  }

  private setAnimationDoor(start: any, end: any, mesh: any) {
    this.tween = new TWEEN.Tween(start) // Create a new tween that modifies 'coords'.
      .to(end, 1000) // Move to (300, 200) in 1 second.
      .easing(TWEEN.Easing.Quadratic.InOut) // Use an easing private to make the animation smooth.
      .onUpdate((that: any) => {
          mesh.rotation.z = that.z
      })
      .start() // Start the tween immediately.
  }

  private setAnimationCamera(start: any, end: any) {
    this.tween = new TWEEN.Tween(start).to(end, 3000).easing(TWEEN.Easing.Quadratic.Out).onUpdate((that: any) => {
      //  camera.postition  和 controls.target 一起使用
      this.camera.position.set(that.cx, that.cy, that.cz)
      this.orbitControls.target.set(that.ox, that.oy, that.oz)
    }).start()
  }

  private onPointClick(event: any) {
    let mouse = new Vector2();
    let raycaster = new Raycaster();
    mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
    mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

    raycaster.setFromCamera(mouse, this.camera);
    const intersects = raycaster.intersectObjects(this.scene.children);
    //在计算的射线上的模型全部变红
    // for (var i = 0; i < intersects.length; i++) {
    //     intersects[i].object.material.color.set(0xff0000);
    // }
    //点击的第一个模型变红
    intersects.forEach((item) => {
      if(item.object.name === "door_rf_primary_0" || item.object.name === 'door_rf_glass0_0') {
        // {x: 1.4981611176025857, y: 1.3434580262073936, z: -0.4344370836680319}
        if(this.carOpenStatus === 'open') {
          this.carClose()
        } else {
          this.carOpen()
        }
      } else if(item.object.name === 'bonnet_ok_primary_0') {
        if(this.carCoverOpenStatus === 'open') {
          this.carFrontCoverClose()
        } else {
          this.carFrontCoverOpen()
        }
      } else if(item.object.name === 'boot_primary_0') {
        if(this.carBackCoverOpenStatus === 'open') {
          this.carBackCoverClose()
        } else {
          this.carBackCoverOpen()
        }
      }
    })
  }
  private mounted() {
    this.init()
    this.renderI()
    window.addEventListener('resize',() => {
      // 重新设置视窗大小
      this.camera.aspect = window.innerWidth / window.innerHeight
      this.camera.updateProjectionMatrix()

      // 重新计算render
      this.renderer.setSize(window.innerWidth, window.innerHeight)
    })
    window.addEventListener('click', this.onPointClick);
  }
}
</script>

