<template>
    <div id="container">

    </div>
</template>

<script lang="ts">
    import { Component, Vue } from 'vue-property-decorator';
    import {
        Scene,
        PerspectiveCamera,
        BoxGeometry,
        MeshBasicMaterial,
        Mesh,
        WebGLRenderer,
        GridHelper,
        AxesHelper,
        Color,
        Fog,
        CubeTextureLoader,
    } from 'three'
    import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
    import GUI from 'lil-gui'
    @Component
    export default class Demo1 extends Vue {
        private scene: any;
        private camera: any;
        private cube: any; // 物体
        private renderer: any; // 渲染器
        private controls: any; // 控制器
        private mounted() {
            // 创建场景
            this.scene = new Scene()
            // 背景颜色
            // this.scene.background = new Color(0x6c6c6c)
            // 添加纹理(添加的图片必须宽高比相等)
            // this.scene.background = new CubeTextureLoader().setPath('/threejsCar/images/').load([
            //     'px.jpg',
			// 	'nx.jpg',
			// 	'py.jpg',
			// 	'ny.jpg',
			// 	'pz.jpg',
			// 	'nz.jpg'
            // ])
            // 添加雾
            // this.scene.fog = new Fog(0xffffff, 8, 14)
            
            // 创建相机
            this.camera = new PerspectiveCamera()
            this.camera.position.z = 10
            this.camera.position.y = 2
            // 创建立方体
            const geometry = new BoxGeometry()
            const material = new MeshBasicMaterial({
                color: 'green'
            })
            
            // 网格
            this.cube = new Mesh(geometry, material)
            this.cube.position.set(0, 2, 0)
            this.scene.add(this.cube)  
            
            // 创建渲染器
            this.renderer = new WebGLRenderer({antialias: true})
            let dom: any = document.getElementById('container')
            dom.appendChild(this.renderer.domElement)
            // 调整渲染器窗口大小
            this.renderer.setSize(window.innerWidth, window.innerHeight)

            // 添加网格地面
            const grid = new GridHelper(10, 10)
            this.scene.add(grid)

            // 添加轨道控制器
            this.controls = new OrbitControls(this.camera, this.renderer.domElement)
            // 监听事件
            // this.controls.addEventListener('change', () => {
            //     console.log('触发了')
            // })
            // 添加阻尼效果使拖动的时候更加圆滑
            this.controls.enableDamping = true;
            // 默认0.05,数值越小惯性越大
            this.controls.dampingFactor = 0.05
            // 自动旋转
            this.controls.autoRotate = false
            // 自动旋转的速率
            // 数值默认2.0,数值越大越快
            this.controls.autoRotateSpeed = 2
            // 禁止平移
            this.controls.enablePan = false

            // 三维坐标轴
            const axesHelper = new AxesHelper(5);
            axesHelper.position.set(0, 2, 0)
            this.scene.add(axesHelper)

            // 参数控制面板
            this.initPanel()

            // 让物体动起来
            this.animate()

            // 重新设置视窗大小
            window.addEventListener('resize', () => {
                this.camera.aspect = window.innerWidth / window.innerHeight
                this.camera.updateProjectionMatrix()
                this.renderer.setSize(window.innerWidth, window.innerHeight)
                this.renderer.update
            })
        }

        private setTarget(x: any, y: any, z: any) {
            console.log(typeof x)
        }

        private animate() {
            requestAnimationFrame(this.animate)
            // this.cube.rotation.x += 0.01

            this.controls.update()
            // 进行渲染
            this.renderer.render(this.scene, this.camera)
        }

        private initPanel() {
            let obj = {
                lookAt: () => {
                    this.camera.position.y = 15
                    this.camera.position.z = 10
                    this.controls.target.y = 2
                    this.controls.update()
                },
                moveCube: () => {
                    this.cube.position.x = 2
                    this.controls.target.set(2, 2, 0)
                    this.controls.update()
                },
                right: 0.1,
                color: '#6c6c6c',
                type: '正常',
                isShow: false
            }
            let gui = new GUI()
            let f = gui.addFolder('配置')
            // 基础按钮
            f.add(obj, 'lookAt').name('改变仰角')
            f.add(obj, 'moveCube').name('移动物体')
            // 数值
            f.add(obj, 'right').min(0.1).max(4).step(0.1).onChange((val: any) => {
                this.cube.position.x = val
            })
            // 颜色选择器
            f.addColor(obj, 'color').onChange((val: any) => {
                this.cube.material.color.set(val)
            })
            // 下拉列表
            f.add(obj, 'type', ['正常','异常','错误']).onChange((val: any) => {
                // output
            })
            // 复选框
            f.add(obj, 'isShow')
            
        }
    }
</script>

